import { Injectable } from '@angular/core';
import { Headers, Http, Response, URLSearchParams } from '@angular/http';
import { Observable } from 'rxjs/Rx';
import 'rxjs/add/operator/toPromise';
//
import { WebService } from '../webapi';
import { BaseService } from '../entities';
import { Context } from '../context';

export class CatalogOptions {
    entidad?: string;
    idEnsamblado?: number;
    ensamblado?: string;
    claseAcciones?: string;
    codigoAutogenerado?: boolean;
    busquedasServidor?: boolean;
    tamanioPagina?: number;
    utilizaCache?: boolean;
    duracionCache?: number;
    logCambios?: boolean;
    tipoSincronizacion?: string;
}

@Injectable()
export class CustomCatalogService extends BaseService {
    constructor(protected http: Http, protected context: Context) {
        super(http, context, 'api/kernel/custom/catalog');        
    }

    load(): Promise<CatalogOptions[]> {
        return this.apiService.getData('load', null)
            .map(response => response.json() as CatalogOptions[])
            .toPromise();
    }



}